.navbar-dark .navbar-toggler {
	color: #fff;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	border-radius: 5px;
	border-color: #fff;
}

.scrollDown {
	position: absolute;
	top: 50vh;
	left: 40vw;
	width: 30px;
	height: 30px;

	transform: translateY(-80px) translateX(-50%) rotate(45deg);
}

.scrollDown span {
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 100%;
	/* border-bottom: 2px solid; */
	border-right: 2px solid;
	-webkit-animation: animate 1.5s linear infinite;
	        animation: animate 1.5s linear infinite;
	opacity: 0;
}

.scrollDown span:nth-child(1) {
	transform: translate(-15px, -15px);
	-webkit-animation-delay: -0.4s;
	        animation-delay: -0.4s;
}

.scrollDown span:nth-child(2) {
	transform: translate(0, 0);
	-webkit-animation-delay: -0.2s;
	        animation-delay: -0.2s;
}

.scrollDown span:nth-child(3) {
	transform: translate(15px, 15px);
	-webkit-animation-delay: 0s;
	        animation-delay: 0s;
}

@-webkit-keyframes animate {
	0% {
		top: -10px;
		left: -10px;
		opacity: 0;
	}

	25% {
		top: 0px;
		left: 0px;
		opacity: 1;
	}

	50%,
	100% {
		top: 10px;
		left: 10px;
		opacity: 0;
	}
}

@keyframes animate {
	0% {
		top: -10px;
		left: -10px;
		opacity: 0;
	}

	25% {
		top: 0px;
		left: 0px;
		opacity: 1;
	}

	50%,
	100% {
		top: 10px;
		left: 10px;
		opacity: 0;
	}
}

::-webkit-scrollbar {
	width: 10px;
	background: inherit;
}

::-webkit-scrollbar-thumb {
	background: linear-gradient(rgb(10, 130, 160), rgb(15, 72, 85));
	border-radius: 10px;
	-webkit-transition: all 1s cubic-bezier(0.165, 0.84, 0.44, 1);
	transition: all 1s cubic-bezier(0.165, 0.84, 0.44, 1);
}

::-webkit-scrollbar-thumb:hover {
	background: linear-gradient(rgb(15, 72, 85), rgb(10, 130, 160));
}

.part {
	position: absolute;
	opacity: 0.5;
	z-index: 0;
}
/* Social Component Styles. */

.right {
    position: fixed;
    bottom: 15vh;
    vertical-align: center;
    left: auto;
    right: 8px;
    z-index: 25;
    color: rgb(100, 255, 218);
}

.email {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    height: auto;
    background: inherit;
}

.email .address {
    font-family: "Fira Code", "Fira Mono", monaco, monospace;
    letter-spacing: 0.1em;
    -webkit-writing-mode: vertical-lr;
    margin: 20px auto;
    color: rgb(100, 255, 218);
    padding: 10px;
}

.email a {
    text-decoration: none;
    display: inline-block;
    position: relative;
    transition: all .25s cubic-bezier(0.165, 0.84, 0.44, 1) 0s;
}

.email a:hover {
    transform: translateY(-5px);
    color: #fff;
}

.email a::after,
.email a::before {
    content: "";
    display: inline-block;
    height: 60px;
    width: 1px;
    margin: 10px auto;
    background: rgb(100, 255, 218);
}


