/* Social Component Styles. */

.right {
    position: fixed;
    bottom: 15vh;
    vertical-align: center;
    left: auto;
    right: 8px;
    z-index: 25;
    color: rgb(100, 255, 218);
}

.email {
    display: flex;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
    position: relative;
    height: auto;
    background: inherit;
}

.email .address {
    font-family: "Fira Code", "Fira Mono", monaco, monospace;
    letter-spacing: 0.1em;
    -webkit-writing-mode: vertical-lr;
    margin: 20px auto;
    color: rgb(100, 255, 218);
    padding: 10px;
}

.email a {
    text-decoration: none;
    display: inline-block;
    position: relative;
    transition: all .25s cubic-bezier(0.165, 0.84, 0.44, 1) 0s;
}

.email a:hover {
    transform: translateY(-5px);
    color: #fff;
}

.email a::after,
.email a::before {
    content: "";
    display: inline-block;
    height: 60px;
    width: 1px;
    margin: 10px auto;
    background: rgb(100, 255, 218);
}

